.cart-buttons {
  height: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1150px) {
  .cart-buttons {
    justify-content: center;
    flex-wrap: wrap;
  }
}
.cart-buttons button {
  background-color: rgb(102, 102, 180);
  padding: 0.5rem;
  border-radius: 1rem;
  width: 67px;
  height: 40px;
  font-weight: bold;
  color: white;
  border: none;
  letter-spacing: 1px;
  margin-right: 1px;
  cursor: pointer;
}
.cart-buttons button svg {
  transform: scale(1.9);
}
@media (max-width: 552px) {
  .cart-buttons button svg {
    transform: scale(1.5) !important;
  }
}
@media (max-width: 1150px) {
  .cart-buttons button {
    width: 45px;
    height: 30px;
  }
}
@media (max-width: 889px) {
  .cart-buttons button {
    width: 33px;
  }
}
@media (max-width: 552px) {
  .cart-buttons button {
    width: 28px;
    height: 27px;
  }
}